@font-face {
  font-family: "Fraunces";
  src: url("/assets/fonts/fraunces/static/Fraunces_144pt-SemiBold.ttf") format("TrueType");
  font-weight: 600;
}
@font-face {
  font-family: "Manrope";
  src: url("/assets/fonts/manrope/static/Manrope-Regular.ttf") format("TrueType");
  font-weight: 400;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.fs--large {
  font-size: 3.125rem;
}
@media (min-width: 45rem) {
  .fs--large {
    font-size: 3.75rem;
  }
}
@media (min-width: 75rem) {
  .fs--large {
    font-size: 5rem;
  }
}

.fs--medium {
  font-size: 2rem;
}
@media (min-width: 45rem) {
  .fs--medium {
    font-size: 3rem;
  }
}
@media (min-width: 75rem) {
  .fs--medium {
    font-size: 3.5rem;
  }
}

.fs-small {
  font-size: 1.75rem;
}
@media (min-width: 75rem) {
  .fs-small {
    font-size: 2rem;
  }
}

body {
  font-size: 1rem;
  font-family: "Manrope", sans-serif;
}
@media (min-width: 75rem) {
  body {
    font-size: 1.125rem;
  }
}

.wrapper {
  width: 100%;
  max-width: 69.5rem;
  margin-inline: auto;
  padding-inline: 1rem;
}
@media (min-width: 45rem) {
  .wrapper {
    padding-inline: 2.5rem;
  }
}
@media (min-width: 75rem) {
  .wrapper {
    padding-inline: 0;
  }
}

.button-cta--1 {
  position: relative;
  color: rgb(255, 255, 255);
  font-weight: 700;
  transition: color 200ms ease-in-out;
}
.button-cta--1:hover {
  color: rgb(68, 255, 161);
}
.button-cta--1::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: rgb(68, 255, 161);
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: bottom;
  justify-content: space-between;
  padding-top: 2.3125rem;
}
@media (min-width: 75rem) {
  .header {
    padding-top: 3.61125rem;
  }
}
.header__nav ul {
  list-style: none;
}
.header__button {
  text-decoration: none;
}